





































import Vue from 'vue'
import OrbitControls from '../../components/OrbitControls.vue'

const vector3 = (x: number, y: number, z: number) => '' + x + ' ' + y + ' ' + z

export default Vue.extend({
    name: 'Tank',
    components: { OrbitControls },
    data: function() {
        return {
            rotation: '',
            rotationX: 0,
            rotationY: 1,
            rotationZ: 0
        }
    },

    mounted: function() {
        requestAnimationFrame(this.animate)
    },
    methods: {
        animate(time: number) {
            time *= 0.001;  // convert time to seconds
            this.rotation = vector3(this.rotationX*time, this.rotationY*time, this.rotationZ*time)

            requestAnimationFrame(this.animate);
        }
    },
})
